const config = {
    env: 'prod',
    get apiIpWithPort() {

        return this.env === 'prod' ? '13.234.225.218:5505' : 'localhost:5505'

    },
    get apiBaseurl() {
        return 'http://' + this.apiIpWithPort
    },
    get apiUrl() {

        // return 'http://192.168.0.15:5505/admin/';
        // return 'http://localhost:5505/admin/';

        return this.apiBaseurl + '/admin/';
    }

}

export default config